import React, { Component } from 'react';
import QRCode from "qrcode.react";
import { Card } from 'reactstrap';


class QRCodeGenerator extends Component {

    constructor(props){
        super(props);
        this.state = {
            qrValue: "",
            isLoaded: false,
            size: 128
        };
    }

    generateQR = (item) => {
        this.setState({
            isLoaded: false,
            size: 0
        });
        
        if(item.length > 0){
            const factor = item.length / 250 + 1;
            
            this.setState({
                qrValue: item,
                isLoaded: true,
                size: 128 * factor
            });
        }
    }

    resetPage =() => {
        this.setState({
            qrValue: "",
            isLoaded: false,
            size: 128
        });
    }

    render() {
        return (
            <Card>
                <h1>QR-Code Generator !!!</h1>
                
                <textarea 
                    rows={5}
                    cols={30}
                    onInput={(item) => { this.generateQR(item.target.value); }}    
                />
                <br />
                <h3>Char(s). count : { this.state.qrValue.length }</h3>
                <br />

                {
                    (this.state.isLoaded) &&
                    <QRCode 
                        size={this.state.size}
                        value={this.state.qrValue}
                    />
                }
            </Card>
        )
    }

}

export default QRCodeGenerator;